import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'

Vue.config.productionTip = false

// 全局引入mint-ui
import MintUI from 'mint-ui'
import 'mint-ui/lib/style.css'
Vue.use(MintUI)

// 使用mint-ui的组件 
import { Loadmore, Indicator } from 'mint-ui';
Vue.component(Loadmore.name, Loadmore);

// 引入axios
import axios from "axios";//引入axios配置文件


const commonUrl = process.env.NODE_ENV == 'development' ? ' http://localhost:8888' : ''
Vue.prototype.$commonUrl = commonUrl


// 配置axios的请求拦截器
axios.interceptors.request.use(function (config) {
  // 发送请求之前，显示loading
  Indicator.open('Loading...')

  config.url = commonUrl + config.url

  // 如果没有请求参数，就给它设置一个默认值，为空对象
  config.data = config.data || {}

  // 再添加一个请求参数，为token
  config.data.token = store.state.token

  return config
}, (error) => {
  return Promise.reject(error)
});

// 配置axios的响应拦截器
axios.interceptors.response.use(function (response) {
  // 接收响应之前，关闭loading
  Indicator.close()

  // 对响应数据做点什么
  return response;
}, function (error) {
  // 对响应错误做点什么
  return Promise.reject(error);
});

//将axios挂载在Vue实例上
Vue.prototype.$axios = axios;




// 全局引入vant
import Vant from 'vant';
import 'vant/lib/index.css';
Vue.use(Vant);
// import { Tabbar, TabbarItem } from 'vant';
// Vue.use(Tabbar).use(TabbarItem);

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
