<template>
  <div class="register">
    <van-nav-bar title="注册" left-text="返回" left-arrow @click-left="onClickLeft" />
    <van-cell-group class="cell">
      <van-field v-model="username" placeholder="请输入用户名" label="用户名" />
      <van-field v-model="mobile" placeholder="请输入手机号" label="手机" />
      <van-field v-model="pwd" placeholder="请输入密码" label="密码" />
      <van-button round type="danger" size="large" @click="register">注册</van-button>
    </van-cell-group>
  </div>
</template>

<script>
import { Toast } from "vant";

export default {
  data() {
    return {
      //   registerInfo: {},
      username: "",
      mobile: "",
      pwd: ""
    };
  },
  methods: {
    onClickLeft() {
      this.$router.go(-1);
    },
    register() {
      const { username, mobile, pwd } = this;
      console.log(username, mobile, pwd )
      this.$axios
        .post("/user/register", { username, mobile, pwd })
        .then(res => {
          var data = res.data;
          if (data.code == 1) {
            //    this.$toast(data.msg)
            this.$router.push({ name: "login" });
          }
          Toast(data.msg);
        });
    }
  }
};
</script>
