<template>
  <div class="cart">
    <Head title="购物车" :show="false"></Head>
    <div class="cell-height">
      <template v-for="(item,index) in list">
      <div class="cell" :key="index">
        <i
          class="iconfont choice"
          :class="[item.chosen ? 'icon-xuanzhong1' : 'icon-icon-test']"
          @click="chooseItem(item,index)"
        ></i>
        <div class="imgzong">
          <img :src="item.goodsImg" class="img1" />
        </div>
        <div class="cart1">
          <div class="name">{{item.goodsName}}</div>
          <div class="item-color">颜色:{{item.goodsColor}}</div>
          <div class="item-size">尺码:{{item.goodsSize}}</div>

          <div class="cart2">
            <div class="price">￥{{item.goodsPrice}}</div>

            <div class="num">
              <van-button
                type="info"
                size="mini"
                @click="decrease(item,index)"
                :disabled="item.goodsNum == 1"
              >-</van-button>
              <span>{{item.goodsNum}}</span>
              <van-button type="info" size="mini" @click="increase(item,index)">+</van-button>
            </div>

            <span class="del" @click="delItem(item,index)">
              <i class="iconfont icon-icon-text-fn-delete"></i>
            </span>
          </div>
        </div>
      </div>
    </template>
    </div>
    <div class="bottom">
      <div class="choice">
        <i
          class="iconfont choice"
          :class="[isAll ? 'icon-xuanzhong1' : 'icon-icon-test']"
          @click="chooseAll"
        ></i>
        <span>全选</span>
      </div>
      <p>
        <span>合计:</span>
        <span class="total">￥{{goodsTotals}}</span>
      </p>
      <van-button plain round type="danger" class="delAll" @click="delAll">批量删除</van-button>
      <van-button plain round type="danger" class="pay" @click="pay">结算</van-button>
    </div>
    <div v-if="!list.length">
      <div class="kong">
        <img
          src="http://ku.90sjimg.com/element_origin_min_pic/00/89/50/8656eda2e5cf049.jpg"
          class="tu"
        />
        <p>您的购物车空空如也</p>
        <a class="tu1" @click="getInput">
          <span>去购物</span>
        </a>
      </div>
    </div>
  </div>
</template>

<script>
import Head from "../components/Head.vue";
import { Toast } from "vant";
import { mapState } from "vuex";
export default {
  data() {
    return {
      list: [],
      goodsTotals: 0
    };
  },
  components: {
    Head
  },
  computed: {
    isAll: {
      get() {
        return (
          this.list.length &&
          this.list.filter(e => e.chosen).length == this.list.length
        );
      },
      set(newVal) {
        this.list = this.list.map(e => {
          e.chosen = newVal;

          return e;
        });
      }
    },
    ...mapState(["token"])
  },
  methods: {
    getInput() {
      this.$router.push("/classify");
    },
    // 获取购物车信息
    reqCartList() {
      return this.$axios
        .post("/user/cart/list", {
          token: this.token
        })
        .then(res => {
          const { code, msg, list } = res.data;
          // console.log(res.data)

          if (code == 1) {
            this.list = list;
          } else {
            Toast(msg);
          }
        });
    },
    updateCart(item, idx) {
      return this.$axios
        .post("/user/cart/update", {
          id: item._id,
          goodsNum: item.goodsNum,
          goodsTotal: item.goodsTotal
        })
        .then(res => {
          const { code, msg } = res.data;

          if (code == 1) {
            this.list.splice(idx, 1, item);
            this.getTotal();
          } else {
            alert(msg);
          }
        });
    },
    increase(item, idx) {
      item.goodsNum++;
      item.goodsTotal = item.goodsNum * item.goodsPrice;
      this.updateCart(item, idx);
    },
    decrease(item, idx) {
      item.goodsNum--;
      item.goodsTotal = item.goodsNum * item.goodsPrice;
      this.updateCart(item, idx);
    },
    // 合计
    getTotal() {
      let total = 0;
      for (let item of this.list) {
        if (item.chosen == true) {
          total += item.goodsNum * item.goodsPrice;
        }
      }
      this.goodsTotals = total;
    },
    // 单独选中商品
    chooseItem(item, idx) {
      // 改变某个商品的选中状态
      item.chosen = !item.chosen;

      // 需要使用数组的变异方法
      this.list.splice(idx, 1, item);
      this.getTotal();
    },
    chooseAll() {
      this.isAll = !this.isAll;
      this.getTotal();
    },
    reqDelCart() {},
    // 单独删除
    delItem(item, idx) {
      this.$axios
        .post("/user/cart/del", {
          token: this.token,
          ids: [item._id]
        })
        .then(res => {
          const { code, msg } = res.data;

          if (code == 1) {
            this.list.splice(idx, 1);
            this.getTotal();
            Toast(msg);
          } else {
            Toast(msg);
          }
        });
    },
    delAll() {
      //获取被选中的购物车列表
      const chosenItems = this.list.filter(e => e.chosen);

      // 如果一个都没选中,就不会往下走
      if (chosenItems.length) {
        // 导出每个购物车id
        const ids = chosenItems.map(e => e._id);

        this.$axios
          .post("/user/cart/del", {
            token: this.token,
            ids
          })
          .then(res => {
            const { code, msg } = res.data;

            if (code == 1) {
              // 批量删除
              const list = this.list;
              for (let i = 0; i < list.length; i++) {
                if (list[i].chosen) {
                  list.splice(i, 1);
                  i--;
                }
              }
              Toast(msg);
              this.getTotal();
            } else {
              Toast(msg);
            }
          });
      } else {
        Toast("请选择商品");
      }
    },
    pay() {
      const goodsList = this.list.filter(e => e.chosen);

      if (goodsList.length) {
        Toast("没有后续逻辑了");
        // this.$axios
        //   .post("/user/order", {
        //     token: this.token,
        //     goodsList
        //   })
        //   .then(res => {
        //     const { code, msg } = res.data;

        //     Toast(msg);
        //     this.$router.push("/order");
        //   });
      } else {
        Toast("请选择商品");
      }
    },
    onClickLeft() {
      this.$router.go(-1);
    },
    onClickRight() {
      this.$router.push("/goodslist");
    },
    //默认全选
    getDetail() {
      // 价格:
      for (let item of this.list) {
        item["chosen"] = false;
      }
      this.getTotal();
    }
  },
  created() {
    this.reqCartList().then(() => {
      this.getDetail();
    });
  }
};
</script>

<style lang="scss" scoped>
.icon-xuanzhong1 {
  color: mediumturquoise;
  font-size: 0.3rem;
}
.iconfont {
  font-size: 0.3rem;
  // padding-left: 0.1rem;
  width: 0.55rem;
  box-sizing: border-box;
  text-align: center;
}
.cell {
  border-bottom: 1px solid rgba(110, 110, 110, 0.8);
  // height: 2rem;
  display: flex;
  align-items: center;
  overflow: hidden;
  padding: 0.2rem;
  box-sizing: border-box;
  // text-align: right;
  .chosen {
    margin-left: 0.3rem;
  }
  .imgzong {
    width: 2.3rem;
    .img1 {
      width: 2.3rem;
      height: 2.5rem;
    }
  }

  .cart1 {
    .name {
      // margin: 0 0.3rem;
      // width: 2rem;
      font-size: 0.24rem;
      text-align: left;
      margin-left: 0.1rem;
    }
    .cart2 {
      width: 100%;
      text-align: left;
      .price {
        margin: 0 0.3rem;
        width: 1rem;
        font-size: 0.3rem;
        color: red;
        display: inline-block;
        height: 1rem;
        line-height: 1rem;
        text-align: left;
      }
      .num {
        display: flex;
        align-items: center;
        display: inline-block;
        height: 1rem;
        line-height: 1rem;
        button {
          width: 0.5rem;
          height: 0.3rem;
          text-align: center;
          line-height: 0.3rem;
          border: 0;
        }
        span {
          margin: 0 0.2rem;
          font-size: 0.24rem;
        }
      }

      .del {
        // margin-left: 0.3rem;
        width: 1rem;

        text-align: center;

        height: 1rem;
        line-height: 1rem;
        // display: inline-block;
        float: right;
        .icon-icon-text-fn-delete {
          font-size: 0.4rem;
          vertical-align: middle;
        }
      }
    }
  }
}
.bottom {
  // border-bottom: 1px solid rgba(110, 110, 110, 0.8);
  height: 1rem;
  display: flex;
  align-items: center;
  padding: 0.1rem;
  box-sizing: border-box;
  background-color: #fff;
  position: fixed;
  bottom: 1rem;
  width: 100%;
  .delAll {
    margin-left: 1rem;
  }
  .pay {
    margin-left: 0.2rem;
  }
  .choice {
    width: 1rem;
    height: 0.4rem;
    line-height: 0.4rem;
    span {
      width: 0.2rem;
    }
  }
  p {
    span {
      display: inline-block;
      width: 1rem;
      height: 0.4rem;
    }
    .total {
      text-align: left;
      width: 1rem;
    }
  }
}
.van-nav-bar {
  background-color: red;
}
.van-nav-bar .van-icon {
  color: #fff;
  font-size: 0.5rem;
}
.van-nav-bar__text {
  color: #fff;
}
.van-nav-bar__title {
  font-size: 0.4rem;
  color: #fff;
}
.cart {
  overflow-y: scroll;
  margin-bottom: 2rem;

  .kong {
    width: 100%;
    text-align: center;

    img {
      height: 2.1rem;
      width: 2.5rem;

      vertical-align: middle;
    }
    p {
      height: 1rem;
      line-height: 1rem;

      font-size: 0.5rem;
    }
    .tu1 {
      display: block;
      height: 1rem;
      line-height: 1rem;
      span {
        border: 1px solid red;
        padding: 0.2rem;
        border-radius: 1rem;
        color: red;
        font-size: 0.3rem;
      }
    }
  }
}
.item-color {
  // float: left;
  margin-left: 0.1rem;
}
.item-size {
  // float: left;
  margin-left: 0.1rem;
}

</style>