<template>
  <div>
    <van-nav-bar :title="title" :left-text="back" :left-arrow="show" @click-left="onClickLeft">
      <van-icon name="search" slot="right" v-if="searchShow" @click="onClickRight" class="search"/>
    </van-nav-bar>
  </div>
</template>

<script>
export default {
  data() {
    return {
      searchShow: true
    };
  },
  props: {
    title: String,
    show: Boolean,
    back: String
  },
  methods: {
    onClickLeft() {
      this.$router.go(-1);
    },
    onClickRight() {
      this.$router.push({ name: "search" });
    }
  },
  mounted() {
    // this.searchShow = this.$route.name == "goodsDetail" ? false : true;
    // this.searchShow = this.$route.name == "cart" ? false : true;
    // this.searchShow = this.$route.name == "my" ? false : true;
    // if(this.$route.name == "search"){
    //   this.searchShow = false
    // }else if(this.$route.name == "my"){
    //    this.searchShow =false
    // }else if(this.$route.name == "cart"){
    //    this.searchShow =false
    // }else if(this.$route.name == "goodsinfo"){
    //    this.searchShow =false
    // }
    if(this.$route.name == "goodsDetail"){
      this.searchShow = false
    }else if(this.$route.name == "user"){
       this.searchShow =false
    }else if(this.$route.name == "search"){
       this.searchShow =false
    }else if(this.$route.name == "cart"){
       this.searchShow =false
    }
  }
};
</script>

<style lang="scss" scoped>


.search{
    font-size: .4rem;
}
</style>

