<template>
  <div>
    <Head title="个人中心" :show="true"></Head>
    <div id="my_btn">
      <div class="username">
        <van-button class="my-login" type="danger" v-show="flag" @click="goLogin">登陆/注册</van-button>
        <div class="my-user" v-show="!flag">
          <div class="welcome">欢迎登录:{{username}}</div>
          <van-button class="exit" round type="danger" size="small" @click="goExit">退出</van-button>
        </div>
      </div>
      <div class="iconBtn">
        <i class="iconfont icon-shezhi"></i>
        <i class="iconfont icon-daohangpingjia"></i>
      </div>
    </div>
    <div class="my-dingdan">
      <i class="iconfont icon-jiantou"></i>
      <p>我的定单</p>
      <div class="dingdan">
        <p>
          <i class="iconfont icon-qianbao"></i>
          <span>待付款</span>
        </p>
        <p>
          <i class="iconfont icon-wuliu"></i>
          <span>查看物流</span>
        </p>
        <p>
          <i class="iconfont icon-daipingjia"></i>
          <span>待评价</span>
        </p>
      </div>
    </div>
    <div class="my-dingdan">
      <i class="iconfont icon-jiantou"></i>
      <p>我的钱包</p>
      <div class="dingdan">
        <p>
          <span>0</span>
          <span>礼品卡</span>
        </p>
        <p>
          <span>0</span>
          <span>优惠券</span>
        </p>
        <p>
          <span>0</span>
          <span>积分</span>
        </p>
      </div>
    </div>
    <div class="shoucang">
      <div class="dingdan">
        <p>
          <i class="iconfont icon-aixin"></i>
          <span>收藏/关注</span>
        </p>
        <p>
          <i class="iconfont icon-liulanjilu"></i>
          <span>浏览记录</span>
        </p>
        <p>
          <i class="iconfont icon-bangzhu"></i>
          <span>帮助中心</span>
        </p>
        <p>
          <i class="iconfont icon-drxx66"></i>
          <span>意见反馈</span>
        </p>
      </div>
      <div class="dingdan">
        <p>
          <i class="iconfont icon-huiyuan"></i>
          <span>会员中心</span>
        </p>
        <p>
          <i class="iconfont icon-jifentixicopy"></i>
          <span>积分商城</span>
        </p>
        <p>
          <i class="iconfont icon-meiriqiandao"></i>
          <span>每日签到</span>
        </p>
        <p>
          <i class="iconfont icon-kefu-tianchong"></i>
          <span>联系客服</span>
        </p>
      </div>
    </div>
  </div>
</template>

<script>
// import { mapState } from "vuex";
import Head from "../components/Head.vue";
export default {
  data() {
    return {
      username: "",
      flag: false
    };
  },
  components: {
    Head
    // ...mapState(["token"]),
    // ...mapState(["username"])
  },

  methods: {
    //登录
    goLogin() {
      this.$router.push({ name: "login" });
    },
    // 获取用户信息
    getUserInfo(token) {
      
      this.$axios
        .post("/user/info", {
          token
        })
        .then(res => {
          const { code, msg, username } = res.data;

          if (code == 1) {
            this.username = username;
            // 调用store对象的方法,改变username的值
            this.$store.commit("username", username);
          } else {
            alert(msg);
          }
        })
        .catch(err => {
          return console.log(err);
        });
    },
    //退出登录
    goExit() {
      localStorage.removeItem("token");
      location.reload();
    }
  },
  mounted() {
    // 获取token
    const token = localStorage.getItem("token");

    // 如果token存在,上传token，获取用户信息
    if (token) this.getUserInfo(token);

    if (token) {
      this.flag = false;
    } else {
      this.flag = true;
    }
  }
};
</script>

 


