import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

import axios from 'axios'

/*
	一般情况下,只需要使用state和mutation就可以完成一个小项目的状态管理
	如果在改变参数的时候需要进行异步操作,就会使用action
*/


export default new Vuex.Store({
  state: {
    token: '',
    username: null
  },
  mutations: {
    token(state, data) {
      state.token = data
    },
    username(state, data) {
      state.username = data
    }
  },
  actions: {
    token({ commit }, data) {
      // action提交的是mutation
      commit('token', data)
    },
    myAction({ state }, data) {
      // 这是一个异步操作，用来添加到购物车，需要传入token、商品id和选中商品的数量
      return axios.post('/user/cart/add', {
        token: state.token,
        goodsId: data.goodsId,
        goodsNum: data.goodsNum,
        goodsName: data.goodsName,
        goodsPrice: data.goodsPrice,
        goodsImg: data.goodsImg,
        goodsColor: data.goodsColor,
        goodsSize: data.goodsSize
      })
    }
  }
})
