<template>
  <div id="app">
    <!-- 路由入口 -->
    <router-view />

    <van-tabbar route v-if="showNav" class="footer">
      <van-tabbar-item replace to="/home" icon="home-o">首页</van-tabbar-item>
      <van-tabbar-item replace to="/classify" icon="shop-o">分类</van-tabbar-item>
      <van-tabbar-item replace to="/cart" icon="shopping-cart-o">购物车</van-tabbar-item>
      <van-tabbar-item replace to="/user" icon="user-o">我的</van-tabbar-item>
    </van-tabbar>
  </div>
</template>

<script>
export default {
  data() {
    return {};
  },
  computed: {
    // 是否显示底部导航
    showNav() {
      // return this.$route.name != "demo"  ;
      if (this.$route.name == "home") {
        return true;
      } else if (this.$route.name == "classify") {
        return true;
      } else if (this.$route.name == "cart") {
        return true;
      } else if (this.$route.name == "user") {
        return true;
      } else {
        return false;
      }
    }
  },
  created() {
    const token = localStorage.getItem("token");

    if (token) this.$store.commit("token", token);
  }
};
</script>

<style lang="scss">
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
