<template>
  <div class="list-container">
    <Head title="列表" :show="true"></Head>
    <!-- 
            1.添加mt-loadmore组件
            2.添加loadTop、loadBottom、allLoaded和ref
            3.将默认的auto-fill改为false
    -->
    <mt-loadmore
      :top-method="loadTop"
      :bottom-method="loadBottom"
      :bottom-all-loaded="allLoaded"
      ref="loadmore"
      :auto-fill="false"
    >
      <!-- 使用v-for渲染商品列表 -->
      <ul class="list">
        <li v-for="(item,index) in list" :key="index" @click="toDetail(item._id)">
          <div><img :src="item.imgs" /></div>
          <p>{{item.title}}</p>
          <p>¥ {{item.price}}</p>
        </li>
      </ul>
    </mt-loadmore>
  </div>
</template>

<script>
import Head from "../components/Head.vue";

export default {
  data() {
    return {
      // 是否全部加载完毕
      allLoaded: false,

      // 列表
      list: [],
      // 页数
      page: 1
    };
  },
  components: {
    Head
  },
  methods: {
    // 从上往下拉,刷新页面
    loadTop() {
      // 获取第一页
      this.$axios
        .post("/list/goodslist", {
          pageNum: 1
        })
        .then(res => {
          const { list } = res.data;

          // 整个列表显示第一页
          this.list = list;
          // 页数为1
          this.page = 1;

          // 去掉正在加载的顶部loading
          this.$refs.loadmore.onTopLoaded();
          // 放开上拉加载更多
          this.allLoaded = false;
        });
    },
    // 从底部往上滑,加载更多
    loadBottom() {
      // 尝试让页数+1
      const newPage = this.page + 1;

      // 尝试获取下一页
      this.$axios
        .post("/list/goodslist", {
          pageNum: newPage
        })
        .then(res => {
          const { list } = res.data;

          // 如果下一页有数据
          if (list.length) {
            // 将数据拼接到旧数据的后面
            this.list = this.list.concat(list);

            // 更改页数
            this.page = newPage;
          } else {
            // 关闭上拉加载更多的功能
            this.allLoaded = true;
          }

          // 关闭正在加载的底部提示
          this.$refs.loadmore.onBottomLoaded();
        });
    },
    // 跳转到商品详情
    toDetail(id) {
      this.$router.push({ name: "goodsDetail", params: { goodId: id } });
    }
  },
  // 页面初始化的时候,手动加载第一页,相当于刷新一次列表
  mounted() {
    this.loadTop();
  }
};
</script>

<style lang="scss" scoped>
.list-container {
  height: 100vh;
  overflow: scroll;
  // padding-bottom: 1rem;
  // box-sizing: border-box;
}

.list {
  li {
    float: left;
    width: 49%;
    border: 1px solid #eee;
    font-size: 0.3rem;
    // text-align: center;

    div{
      text-align: center;
    }

    p{
      box-sizing:border-box;
      padding-left: 0.1rem;
    }

    img {
      width: 3.5rem;
      height: 3.5rem;
      object-fit: cover;
    }
  }
}
</style>