<template>
  <div>
    <Head title="商品详情" :show="true"></Head>
    <div class="goods">
      <van-swipe class="goods-swipe" :autoplay="3000">
        <van-swipe-item v-for="thumb in goods.thumb" :key="thumb">
          <img :src="good.imgs" />
        </van-swipe-item>
      </van-swipe>

      <van-cell-group>
        <van-cell>
          <div class="goods-title">{{ good.title }}</div>
          <div class="goods-price">{{ formatPrice(good.price) }}</div>
        </van-cell>
        <van-cell class="goods-express">
          <van-col span="10">运费：{{ goods.express }}</van-col>
          <van-col span="14">销量：{{ good.payNum }}</van-col>
        </van-cell>
      </van-cell-group>

      <van-cell-group class="goods-cell-group">
        <van-cell value="进入店铺" icon="shop-o" is-link @click="sorry">
          <template slot="title">
            <span class="van-cell-text">{{good.shopNick}}</span>
            <van-tag class="goods-tag" type="danger">官方</van-tag>
          </template>
        </van-cell>
        <!-- <van-cell title="线下门店" icon="location-o" is-link @click="sorry" /> -->
      </van-cell-group>

      <van-cell-group class="goods-cell-group">
        <van-cell title="请选择商品规格" is-link @click="flagShow">{{select1}} {{size1}}</van-cell>
      </van-cell-group>

      <van-goods-action>
        <van-goods-action-icon icon="chat-o" @click="sorry">客服</van-goods-action-icon>
        <van-goods-action-icon icon="cart-o" @click="onClickCart">购物车</van-goods-action-icon>
        <van-goods-action-button type="warning" @click="addCart">加入购物车</van-goods-action-button>
        <van-goods-action-button type="danger" @click="sorry">立即购买</van-goods-action-button>
      </van-goods-action>
    </div>

    <div class="cl">
      <van-action-sheet class="info" v-model="showinfo" title="请选择商品规格">
        <div class="info-div cl">
          <img class="info-img" :src="good.imgs" alt />
          <p class="info-price">¥{{good.price}}</p>
          <p class="info-color">已选:{{select1}} {{size1}}</p>
        </div>
        <div class="goods-color cl">
          <div class="goods-yanse">颜色</div>
          <ul class="color cl">
            <li
              class="item2"
              v-for="(item2,index2) in select"
              :key="index2"
              @click="selectColor(item2,$event)"
            >{{item2.color}}</li>
          </ul>
        </div>
        <div class="info-size">
          <div class="goods-size">尺码</div>
          <ul class="size cl">
            <li
              class="item"
              v-for="(item,index) in size"
              :key="index"
              @click="selectSize(item,$event)"
            >{{item.txt}}</li>
          </ul>
        </div>
      </van-action-sheet>
    </div>
  </div>
</template>

<script>
import {
  Tag,
  Col,
  Icon,
  Cell,
  CellGroup,
  Swipe,
  Toast,
  SwipeItem,
  GoodsAction,
  GoodsActionIcon,
  GoodsActionButton,

  Dialog 
} from "vant";

import Head from "../components/Head.vue";
import { mapState } from "vuex";

export default {
  components: {
    [Tag.name]: Tag,
    [Col.name]: Col,
    [Icon.name]: Icon,
    [Cell.name]: Cell,
    [CellGroup.name]: CellGroup,
    [Swipe.name]: Swipe,
    [SwipeItem.name]: SwipeItem,
    [GoodsAction.name]: GoodsAction,
    [GoodsActionIcon.name]: GoodsActionIcon,
    [GoodsActionButton.name]: GoodsActionButton,

    //引入头部
    Head
  },
  computed: {
    // 如果有多个计算属性,添加mapState的时候,需要使用扩展运算符
    ...mapState(["username"])
  },
  data() {
    return {
      goodId: this.$route.params.goodId, //获取列表传过来的_id
      good: {},
      goods: {
        title: "美国伽力果（约680g/3个）",
        price: 2680,
        express: "免运费",
        remain: 19,
        thumb: [
          "https://img.yzcdn.cn/public_files/2017/10/24/e5a5a02309a41f9f5def56684808d9ae.jpeg",
          "https://img.yzcdn.cn/public_files/2017/10/24/1791ba14088f9c2be8c610d0a6cc0f93.jpeg"
        ]
      },
      showinfo: false,
      select: [
        { color: "黑色" },
        { color: "红色" },
        { color: "白色" },
        { color: "灰色" }
      ],
      size: [
        { txt: "32" },
        { txt: "33" },
        { txt: "34" },
        { txt: "35" },
        { txt: "36" },
        { txt: "37" },
        { txt: "38" },
        { txt: "39" }
      ],
      size1: "",
      select1: "",
      num: 1
    };
  },
  methods: {
    flagShow() {
      this.showinfo = true;
    },
    formatPrice() {
      return "¥" + ((this.good.price * 100) / 100).toFixed(2);
    },
    onClickCart() {
      this.$router.push("cart");
    },
    sorry() {
      Toast("暂无后续逻辑~");
    },
    selectColor(item, event) {
      this.select1 = item.color;
      // console.log(this.size1);
      let li_dom = document.getElementsByClassName("item2");
      for (let i = 0; i < li_dom.length; i++) {
        li_dom[i].style = "border:1px #aaa solid";
      }
      event.target.style.border = "1px #f00 solid";
    },
    selectSize(item, event) {
      this.size1 = item.txt;
      // console.log(this.size1);
      let li_dom = document.getElementsByClassName("item");
      for (let i = 0; i < li_dom.length; i++) {
        li_dom[i].style = "border:1px #aaa solid";
      }
      event.target.style.border = "1px #f00 solid";
    },
    addCart() {
      // alert(this.goodId)
      const token = localStorage.getItem("token");
      if (token) {
        if (this.select1 && this.size1) {
          // alert(this.username);

          this.$store
            .dispatch("myAction", {
              goodsId: this.goodId,
              goodsName: this.good.title,
              goodsPrice: this.good.price,
              goodsImg: this.good.imgs,
              goodsNum: this.num,
              goodsColor: this.select1,
              goodsSize: this.size1
            })
            .then(res => {
              const { code, msg } = res.data;

              if (code == 1) {
                // 当添加成功，就弹出下面这个提示框
                // if (confirm("加入购物车成功，是否进入购物车列表"))
                //   this.$router.push("/cart");
                Dialog.confirm({
                  title: "标题",
                  message: "加入购物车成功，是否进入购物车列表"
                })
                  .then(() => {
                    this.$router.push("/cart");
                  })
                  .catch(() => {
                    // on cancel
                  });
              } else {
                alert(msg);
              }
            });
        } else {
          this.showinfo = true;
        }
      } else {
        if (confirm("还未登录，是否进入登录界面")) {
          this.$router.push("login");
        }
      }
    }
  },
  created() {
    // alert(this.goodId);
    this.$axios
      .post("/list/getGoodOne", {
        //用_id去数据库查询数据 并将结果赋值给good
        goodId: this.goodId
      })
      .then(res => {
        this.good = res.data.data;
        this.show = true;
        // console.log(this.good);
      });
  }
};
</script>

<style lang="scss" >
.goods {
  padding-bottom: 50px;
  &-swipe {
    img {
      width: 100%;
      display: block;
    }
  }
  &-title {
    font-size: 16px;
  }
  &-price {
    color: #f44;
  }
  &-express {
    color: #999;
    font-size: 12px;
    padding: 5px 15px;
  }
  &-cell-group {
    margin: 15px 0;
    .van-cell__value {
      color: #999;
    }
  }
  &-tag {
    margin-left: 5px;
  }
}
.info {
  box-sizing: border-box;
  padding-left: 0.2rem;
  .info-div {
    overflow: hidden;
    .info-img {
      width: 2rem;
      float: left;
      margin-right: 0.4rem;
    }
    .info-price {
      // float: left;
      color: red;
      font-size: 0.35rem;
      margin-top: 0.2rem;
    }
    .info-color {
      // float: left;
      font-size: 0.3rem;
      margin-top: 0.2rem;
    }
  }
  .goods-color {
    margin-bottom: 0.3rem;
    .goods-yanse {
      margin-top: 0.3rem;
      font-size: 0.4rem;
      margin-bottom: 0.3rem;
    }
    .item {
      float: left;
      border: 1px solid #aaa;
      width: 1.2rem;
      line-height: 0.5rem;
      text-align: center;
      margin-left: 0.2rem;
      margin-top: 0.3rem;
    }
    .item2 {
      float: left;
      border: 1px solid #aaa;
      width: 1.2rem;
      line-height: 0.5rem;
      text-align: center;
      margin-left: 0.2rem;
      // margin-top: 0.3rem;
    }
  }
  .info-size {
    .goods-size {
      font-size: 0.4rem;
    }
    .size {
      margin-bottom: 2rem;
      .item {
        float: left;
        border: 1px solid #aaa;
        width: 1.2rem;
        line-height: 0.5rem;
        text-align: center;
        margin-left: 0.2rem;
        margin-top: 0.3rem;
      }
    }
  }
}
</style>