<template>
  <div>
    <Head title="分类" :show="true"></Head>
    <div class="cl my-div">
      <van-sidebar :active-key="activeKey" @change="onChange" class="content-left">
        <van-sidebar-item
          v-for="(item,index) in data"
          :key="index"
          :title="item"
          @click="getTitle(item)"
        />
      </van-sidebar>
      <div class="content-right">
        <ClassifyList :goods-title="goodstitle" />
      </div>
    </div>
  </div>
</template>

<script>
import ClassifyList from "../components/ClassifyList.vue";

import Head from "../components/Head.vue";

export default {
  data() {
    return {
      activeKey: 0,
      goodstitle: "",
      data: ["全部品牌", "女鞋", "男鞋", "运动", "户外", "童鞋童装"]
    };
  },
  components: {
    ClassifyList,
    Head
  },
  methods: {
    onChange(key) {
      this.activeKey = key;
    },
    getTitle(title) {
      this.goodstitle = title;
      // alert(title);
    }
  },
  mounted() {
    this.goodstitle = "全部品牌";
  }
};
</script>

<style lang="scss" scoped>
.my-div {
  margin-bottom: 1rem;
}
.content-left {
  width: 25%;
  float: left;
  font-size: 0.24rem;
}
.content-right {
  width: 75%;
  height: 90vh;
  float: left;
  overflow-y: scroll;
}
</style>
