<template>
  <div class="my-div">
    <div v-for="(item,index) in list" :key="index">
      <p v-show="false">{{item.firstLevel.name}}</p>
      <ul>
        <div v-for="(item2,index2) in item.firstLevel.secondLevel" :key="index2">
          <li class="cl" v-if="item.firstLevel.name==goodsTitle">
            <p class="my-title">{{item2.name}}</p>
            <div v-for="(item3,index3) in item2.thirdLevel" :key="index3" class="my-goods" @click="toGoodsList()">
              <img :src="item3.image" alt class="my-img" />
              <p class="my-p">{{item3.name}}</p>
            </div>
          </li>
        </div>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
    //   goodstitle: "女鞋",
      list: []
    };
  },
  props: ["goodsTitle"],
  methods: {
    showList() {
      this.$axios.post("/list/classify", {}).then(res => {
        const data = res.data.data;
        this.list = data;
        // console.log(this.list);
      });
    },
    toGoodsList(){
      this.$router.push('/goodslist')
    }
  },
  mounted() {
    this.showList();
  }
};
</script>

<style lang="scss" scoped>
.my-div{
  text-align: center;
}
.my-title {
  height: 0.8rem;
  line-height: 0.8rem;
  background-color: #eee;
  font-size: 0.24rem;
}
.my-goods {
  width: 33%;
  float: left;
  .my-img {
    width: 100%;
  }
}
</style>
