<template>
  <div>
    <Head title="搜索" :show="true"></Head>
    <van-search v-model="value" placeholder="请输入搜索关键词" show-action shape="round" @search="onSearch">
      <div slot="action" @click="onSearch">搜索</div>
    </van-search>

    <!-- 使用v-for渲染商品列表 -->
    <ul class="list">
      <li v-for="(item,index) in goods" :key="index" @click="toDetail(item._id)">
        <div>
          <img :src="item.imgs" />
        </div>
        <p>{{item.title}}</p>
        <p>¥ {{item.price}}</p>
      </li>
    </ul>
  </div>
</template>
<script>
import Head from "../components/Head.vue";
export default {
  data() {
    return {
      value: "",
      goods: []
    };
  },
  components: {
    Head
  },
  methods: {
    onSearch() {
      this.$axios
        .post("/list/search", {
          keyword: this.value
        })
        .then(res => {
          this.goods = res.data.data;
        });
    },
    toDetail(id) {
      this.$router.push({ name: "goodsDetail", params: { goodId: id } });
    }
  }
};
</script>

<style lang="scss" scoped>
.list {
  li {
    float: left;
    width: 49%;
    border: 1px solid #eee;
    // text-align: center;
    font-size: 0.3rem;

    div {
      text-align: center;
    }

    p {
      box-sizing: border-box;
      padding-left: 0.1rem;
    }
    img {
      width: 3.5rem;
      height: 3.5rem;
      object-fit: cover;
    }
  }
}
</style>

