<template>
  <div id="login">
    <van-nav-bar
      title="登录"
      left-text="返回"
      right-text="注册"
      left-arrow
      @click-left="onClickLeft"
      @click-right="onClickRight"
    />
    <van-cell-group class="cell">
      <van-field v-model="username" placeholder="请输入用户名" label="用户名" />
      <van-field v-model="pwd" placeholder="请输入密码" label="密码" />
      <van-button round type="danger" size="large" @click="login">登录</van-button>
    </van-cell-group>
  </div>
</template>

<script>
import { Toast } from "vant";

export default {
  data() {
    return {
      // loginInfo: {},
      username: "",
      pwd: ""
    };
  },
  methods: {
    onClickLeft() {
      this.$router.go(-1);
    },
    onClickRight() {
      this.$router.push({ name: "register" });
    },
    login() {
      const { username, pwd } = this;
      this.$axios
        .post("/user/login", { username, pwd })
        .then(res => {
          const { msg, code, token } = res.data;

          if (code == 1) {
            // 登录成功，先把token存在本地
            localStorage.setItem("token", token);

            // 然后,把它存在store里面
            this.$store.commit("token", token);

            Toast(msg);
            this.$router.push({ name: "user" });
          } else {
            Toast(msg);
          }
          // this.$router.push({ name: "home" });
          // Toast(res.data.msg);
        })
        .catch(err => {
          return console.log(err);
        });
    }
  }
};
</script>

<style scoped>
#login {
  background-color: #000;
}
</style>
